<template>
  <div class="box">
    <div class="panel">
      <div class="box-img">
        <img :src="'/assets/img'+this.$route.params.id+'1.png'" alt />
      </div>
      <div class="text">
        <p class="name">货号 : {{ $route.params.style }}</p>
        <div class="share">
          <span>库存</span>:
          <span>{{ $route.params.count }}</span>
        </div>
      </div>
    </div>
    <div class="imgView">
      <div class="imgs-container">
        <img
          :src="img"
          :index="$route.params.id"
          v-for="(img, index) in imgs[this.$route.params.id-1]"
          :key="img"
          @click="handleImgsClick(index)"
        />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      publicPath: process.env.BASE_URL,
      initialIndex: 0,
      imgs: [
        ["/assets/img11.png", "/assets/img12.png"],
        ["/assets/img21.png", "/assets/img22.png"],
        ["/assets/img31.png", "/assets/img32.png"],
        ["/assets/img41.png", "/assets/img42.png"],
        ["/assets/img51.png", "/assets/img52.png"],
        ["/assets/img61.png", "/assets/img62.png"],
        ["/assets/img71.png", "/assets/img72.png"],
        ["/assets/img81.png", "/assets/img82.png", "/assets/img83.png"]
      ] // 使用 :src 调用了 v-bind 指令处理其内容，相对路径不会被webpack的 file-loader 处理
    };
  },
  methods: {
    handleImgsClick(index) {
      this.initialIndex = index;
      const params = {
        $props: {
          imgs: this.imgs[this.$route.params.id - 1],
          initialIndex: "initialIndex", // 响应式数据的key名
          loop: false
        },
        $events: {
          change: i => {
            // 必须更新 initialIndex
            this.initialIndex = i;
          }
        }
      };
      this.$createImagePreview({ ...params }).show();
    }
  }
};
</script>
